import React from "react";
import {
  HashRouter, 
  Routes,
  Route
} from "react-router-dom";
import About from "./routes/About";
import Home from "./routes/Home";
import "./App.css";
import Nav from "./components/Nav";

export default function App() {
  return ( 
      <HashRouter>
        <Nav />
        <Routes><Route exact path="/" render={()=>{<Home />}} element={<Home />} /></Routes>
        <Routes><Route path="/about" render={()=>{<About />}}  element={<About />} /></Routes>
      </HashRouter>
  );
}